<template>
  <v-app class="white">
    <v-toolbar class="white">
      <v-toolbar-title centered><router-link to="/" tag="span" style="cursor: pointer">KYAKU STORE</router-link></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-xs-only">
        <v-btn flat v-for="item in menuItems" :key="item.title" :to="item.link">
          <v-icon left>{{item.icon}}</v-icon>
          {{item.title}}
        </v-btn>
        <v-btn flat v-if="usuarioLogado" @click="onLogout" >
          <v-icon left>exit_to_app</v-icon>
          LOGOUT
        </v-btn>  
      </v-toolbar-items>
      <v-menu bottom left class="hidden-md-and-up">
        <v-btn icon slot="activator">
          <v-icon>more_vert</v-icon>
        </v-btn>
        <v-list>
          <v-list-tile v-for="item in menuItems" :key="item.title" :to="item.link">
            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            <v-list-tile-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-tile-action>
          </v-list-tile>
        </v-list>
        <v-list>
          <v-list-tile flat v-if="usuarioLogado" @click="onLogout">
            <v-list-tile-title>SAIR</v-list-tile-title>
            <v-list-tile-action>
              <v-icon>exit_to_app</v-icon>
            </v-list-tile-action>
          </v-list-tile>
        </v-list>
      </v-menu>
    </v-toolbar>
    <v-container>
      <main>
        <router-view></router-view>
      </main>
    </v-container>
    <v-footer class="white">
      <div>© {{ new Date().getFullYear() }} Criado por Derk Wola. Todos os direitos reservados.</div>
      <v-spacer></v-spacer>
      <div style="font-size: 2em; color: black">
          <a href="https://www.facebook.com/kyakubijuterias/" style="color:black"><i class="fab fa-facebook-f"></i></a>
          <a href="https://www.instagram.com/kyaku_joias_acessorios/" style="color: black"><i class="fab fa-instagram"></i></a>
      </div>
    </v-footer>
</v-app>
</template>
<script>
  export default {
    data () {
      return{
        search: '',
        sideNav: false,
      }
    },
    computed:{
      menuItems(){
        let menuItems = [
          {icon: 'list', title: 'VER PRODUTOS',link: '/produtos'},
          {icon: 'perm_contact_calendar', title: 'CONTACTOS',link: '/contactos'},
          {icon: 'lock_open', title: 'ENTRAR', link: '/signin'}
        ]
        if(this.usuarioLogado){
          menuItems = [
            {icon: 'list', title: 'VER PRODUTOS',link: '/produtos'},
            {icon: 'face', title: 'CRIAR USUARIO', link: '/signup'},
            {icon: 'person', title: 'PERFIL',link: '/perfil'},
            {icon: 'add', title: 'ADICIONAR PRODUTO',link: '/produtos/criar'}
          ]
        }
        return menuItems
      },
      usuarioLogado(){
        return this.$store.getters.user !== null && this.$store.getters.user !== undefined
      }
    },
    methods: {
      onLogout(){
        this.$store.dispatch('logout')
      }
    }
  }
</script>
<style lang="stylus">
  @import 'stylus/main.styl'
  .md{
    padding-bottom: 0px;
  }
</style>



