<template>
  <v-container fluid grid-list-md>
    <v-layout row wrap>
      <v-flex d-flex xs4 offset-xs3 class="hidden-md-and-down">
        <img src="https://firebasestorage.googleapis.com/v0/b/kyakustore-543b7.appspot.com/o/anuncie.svg?alt=media&token=05448307-a22e-4eb2-a65c-798728ad2950" alt="Anuncie Aqui">
      </v-flex>
    </v-layout>
    <v-carousel>
      <v-carousel-item v-for="(item,i) in items" v-bind:src="item.src" :key="i" @click="carro(item.link)">
        <div class="title">
          {{ item.title }}
        </div>
      </v-carousel-item>
    </v-carousel>
    <v-layout row wrap hidden-md-and-up>
      <v-flex xs12 sm6 class="text-xs-center text-sm-right">
        <v-btn large  to="/produtos" color="amber">Explorar Produtos</v-btn>
      </v-flex>
      <v-flex xs12 sm6 class="text-xs-center text-sm-left">
        <v-btn large  to="/info" color="amber">Informações</v-btn>
      </v-flex>
    </v-layout><br>
    <h1 class="display-1">NOVIDADES</h1>
    <v-layout row wrap>
      <v-flex d-flex xs12 sm6 md4 v-for="i in produtos" :key="i.id">
        <v-card hover>
          <v-card-media :src="i.imageUrl" height="400px"></v-card-media>
          <v-card-title>
              <div>
                <h3 class="dc">{{i.nome}}</h3>
                <span class="desc">PREÇO</span>
                <h2 class="preco">$ {{i.preco}}</h2>
            </div>
          </v-card-title>
          <v-card-actions>
            <v-btn flat :to="'/produtos/' + i.id" color="amber">Ver mais</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data () {
      return {
        items: [
          {
            src: 'https://firebasestorage.googleapis.com/v0/b/kyakustore-543b7.appspot.com/o/pub%2Fpastas.jpg?alt=media&token=8d051ca0-c73a-4225-85e8-cf3b443849bf',
            title: 'Venha conhecer os nossos produtos, aqui ira encontrar a mais diversas jóias e acessórios',
            link: '/produtos'
          },
          {
            src: 'https://firebasestorage.googleapis.com/v0/b/kyakustore-543b7.appspot.com/o/pub%2FFotografia.png?alt=media&token=9c50cd78-667c-40ee-af9f-4c257ebc6ef1',
            title: 'Podera nos encontrar nas redes sociais',
            link: '/contactos'
          }
        ]
      }
    },
    computed: {
      produtos() {
        return this.$store.getters.nProdutos
      },
      carregando(){
        return this.$store.getters.carregando
      }
  },
    methods: {
      carregandoProduto(id){
        this.$router.push('/produtos/' + id)
      },
      carro(link){
        this.$router.push(link)
      }
    }
}
</script>

<style scoped>
.contacto{
  background-color:white ;
}
.desc{
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    color: #f1c40f;
}
.dc{
    font-size: 16px;
}
.preco{
    font-size: 22px;
    font-style: italics;
}
.title {
    position: absolute;
    bottom: 50px;
    background-color: rgba(0,0,0,0.5);
    color: white;
    font-size: 2em;
    padding: 20px;
  }
</style>



