<template>
  <v-container fluid grid-list-md>
    <v-layout row wrap>
      <v-flex d-inline-flex xs12 sm6 md4>
        <v-card text-xs-center class="hidden-md-and-down" color="white">
          <v-btn-toggle v-model="search">
            <v-btn flat value="Acessorios" color="grey darken-4">Acessorios</v-btn>
            <v-btn flat value="Anel" color="grey darken-4">Aneis</v-btn>
            <v-btn flat value="Bolsas" color="grey darken-4">Bolsas</v-btn>
            <v-btn flat value="Brincos" color="grey darken-4">Brincos</v-btn>
            <v-btn flat value="Conjuntos" color="grey darken-4">Conjuntos</v-btn>
            <v-btn flat value="Makeup" color="grey darken-4">Makeup</v-btn>
            <v-btn flat value="Oculos" color="grey darken-4">Oculos</v-btn>
            <v-btn flat value="Pulseira"  color="grey darken-4">Pulseiras</v-btn>
            <v-btn flat value="" color="grey darken-4">Tudo</v-btn>
          </v-btn-toggle>
        </v-card>
      </v-flex>
      <v-flex xs6 class="hidden-md-and-up">
        <v-select v-bind:items="items" item-text="txt" item-value="txt" v-model="search" label="CATEGORIA">
        </v-select>
      </v-flex>
    </v-layout>
    <v-layout row wrap>      
      <v-flex d-flex xs12 sm6 md4 v-for="i in produtos" :key="i.id">
        <v-card hover>
          <v-card-media :src="i.imageUrl" height="400px"></v-card-media>
          <v-card-title>
            <div>
                <h3 class="pr">{{i.nome}}</h3>
                <h2 class="preco">$ {{i.preco}}</h2>
            </div>
          </v-card-title>
          <v-card-actions>
          <v-btn flat :to="'/produtos/' + i.id" color="amber">Ver mais</v-btn>
        </v-card-actions>
      </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data(){
    return{
      search: '',
      items: [
        {txt: 'Acessorios'},
        {txt: "Anel"},
        {txt: "Bolsas"},
        {txt: "Brincos"},
        {txt: "Conjuntos"},
        {txt: "Makeup"},
        {txt: "Oculos"},
        {txt: "Pulseira"}       
      ]
    }
  },  
  computed: {
    produtos(){
      return this.$store.getters.carregaProdutos.filter((produtos) => {
        return produtos.categoria.match(this.search)
      })
    }
  }
}
</script>

<style scoped>
.desc{
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    color: #f1c40f;
}
.dc{
    font-size: 16px;
}
.pr{
  color: #f1c40f;
  font-size: 16px;
}
</style>



